/* src/App.css */
@font-face {
  font-family: 'Kruti Dev';
  src: url('./assets/fonts/Kruti_dev.ttf') format('truetype');
  font-weight: normal;
  font-style: normal;
}

.active{
  color: rgb(209 213 219 / var(--tw-text-opacity));
  --tw-text-opacity:1;
    background-color: rgba(255, 255, 255, 0.17);

}

.shadow-light,
.hover\:shadow-light:hover {
  text-shadow: 1px 1px 0 rgba(244, 244, 240, 1), 2px 2px 0 rgba(81, 76, 77, .3);
}

input:focus,
textarea:focus {
  box-shadow: inset 0 0 6px rgba(0,0,0,.4);
  outline: none;
 }

.button {
  @layer .inline-block .mt-6 .rounded .shadow .py-2 .px-4 .no-underline .border-0;
}

/* @layer doesn't work on code pen. */
/* The above renders out to: */
.button {
    display: inline-block;
    margin-top: 1.5rem;
    border-radius: .25rem;
    box-shadow: 0 2px 4px 0 rgba(0, 0, 0, 0.10);
    padding-top: 0.5rem;
    padding-bottom: 0.5rem;
    padding-left: 1rem;
    padding-right: 1rem;
    text-decoration: none;
    border: 0;
}

.overflow-y-hidden {
  overflow-y: hidden;
}

.overflow-x-hidden {
  overflow-x: hidden;
}

.border-x-0 {
  border-left: 0;
  border-right: 0;
}

.border-y-0 {
  border-top: 0;
  border-bottom: 0;
}

.hover\:shadow:hover {
  @layer .shadow;
}

.correct {
  color: black;
  background-color: lightgreen;
}
.missing {
  color: white;
  background-color: red;
}
.spelling-error {
  color: white;
  background-color: orange;
}
textarea {
  width: 100%;
  height: 150px;
  margin-bottom: 10px;
}
.output {
  border: 1px solid #ccc;
  padding: 10px;
  margin-top: 10px;
  white-space: pre-wrap;
}
